<template>
  <div class="listBig">
    <div class="list_box">
      <div class="img">
        <img
          :src="`${item.bookCover}`"
          alt
        >
      </div>
      <div class="write">
        <h2>{{item.bookName}}</h2>
        <p class="p1">{{item.bookInfo}}</p>
        <p class="p2">{{item.bookPrice|money}}</p>
        <div class="btn">
          <!-- 点击删除按钮把该条数据从vuex中删除 -->
          <button @click='remove'>删除</button>
          <button @click='collect' v-if='isShow'>收藏</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // props: ['item'],
  props:{
            item:{
                default(){
                    //对象或者 数组 必须是函数
                    return{

                    }
                }
            },
            isShow:{
                default:true
            }
        },

  methods: {
    remove(){
      // 改变vuex中的数据 只能通过mutations中的函数修改数据
      // this.$store.commit('removeList',this.item)
      this.$emit('mydel',this.item)
      // 只负责触发父组件的函数 并传递一些参数
    },
    collect(){
      this.$store.commit('addCollect',this.item)
    }
  },
};
</script>

<style lang="less">
.listBig {
  position: relative;
  top: 1rem;
  left: 0;
  padding-bottom: 1.05rem;

  .list_box {
    width: 100%;
    height: 3.5rem;
    overflow: hidden;
    border-bottom: 0.01rem solid #ccc;
    display: flex;
    .img {
      flex: 1;
      width: 35%;
      height: 3.5rem;
      img {
        width: 100%;
      }
    }
    .write {
      width: 70%;
      height: 3.5rem;
      h2 {
        font-size: 0.7rem;
      }
      .p1 {
        font-size: 0.48rem;
      }
      .p2 {
        font-size: 0.35rem;
        color: red;
      }
      .btn {
        width: 100%;
        height: 0.95rem;
        button {
          display: block;
          float: left;
          width: 1rem;
          height: 0.6rem;
          border-radius: 0.18rem;
          margin-right: 0.4rem;
          background: red;
          border: 0.01rem solid red;
          margin-top: 0.3rem;
        }
      }
    }
  }
}
</style>