<template>
    <div>
        <mytitle>
            列表页
        </mytitle>
        <mylist v-for='item in list' :key="item.bookId" @mydel="fn" :item='item'></mylist>
    </div>
</template>

<script>
import title from '../../comon/title.vue'
import mylist from './list.vue'
    export default {
        methods: {
            fn(item) {
                this.$store.commit('removeList',item)
            }
        },
        components: {
          'mytitle':title,
          mylist,
        },
        created () {
            if(this.$store.state.isGetList)return
            // 这个请求需要一个判断，请求之后不用再去请求了
            this.$store.dispatch('getListData')
            // console.log(1)
        },
        computed: {
            // 使用计算属性 去使用 state中的 listData
            list(){
                // list 代表的就是后台返回的那些数据
                return this.$store.state.listData;
            }
        }
    }
</script>

